import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  FormControl,
  InputLabel,
  makeStyles,
  Snackbar,
  TextField,
  Typography,
  useMediaQuery,
  useTheme,
  Select,
} from "@material-ui/core";
import { Form, Formik } from "formik";
import MenuItem from "@mui/material/MenuItem";
import warning from "./../../../assets/images/state/warning.svg";
import IdentityDocument from "./../../../assets/images/profile/IdentityDocument.svg";
import adressMap from "./../../../assets/images/passport.jpeg";
import { useDispatch, useSelector } from "react-redux";
import {
  getProfileBalance,
  getProfileData,
  getProfileSumsubID,
  setVerification,
  verificationProfilePersonal,
} from "../../../redux/reducers/myProfile";
import { Alert } from "@mui/material";
import Reload from "../../../components/Reload";
import Saccess from "../../../assets/images/state/saccess-lg.webp";
import time from "../../../assets/images/state/time-lg.webp";
import none from "../../../assets/images/state/none.webp";

const Verification = ({ t }) => {
  const dispatch = useDispatch();

  const { verificationAnswer, profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.up("md"));
  const useStyles = makeStyles((theme) => ({
    warningText: {
      color: "#F59E0B",
      marginLeft: 20,
      fontWeight: 300,
      marginBottom: 16,
      maxWidth: 700,
      [theme.breakpoints.down("md")]: {
        fontSize: 16,
        marginBottom: 8,
        fontWeight: 400,
      },
    },
    warningParagraph: {
      paddingLeft: 34,
      fontWeight: 300,
      marginBottom: 16,
      maxWidth: 700,
      [theme.breakpoints.down("md")]: {
        fontSize: 16,
        marginBottom: 8,
        fontWeight: 400,
      },
    },
    verificationInput: {
      backgroundColor: "#fff",
      boxShadow: "0px 1px 0px #E2E8F0",
      borderRadius: 8,
      marginBottom: 10,
    },

    verificationInputWithoutBg: {
      backgroundColor: "#F7F8F9",
      boxShadow: "0px 1px 0px #E2E8F0",
      borderRadius: 8,
      marginBottom: 10,
    },
    VerificationBlock: {
      background: "#FFFFFF",
      boxShadow: "0px 1px 0px #E2E8F0",
      borderRadius: 8,
      padding: 40,
      flex: 2,
      [theme.breakpoints.down("md")]: {
        padding: 20,
      },
    },
    dragonDrop: {
      border: "2px dashed #000",
      borderRadius: 8,
      padding: 8,
      textAlign: "center",
      marginTop: 10,
      cursor: "pointer",
      fontWeight: 600,
    },
    VerificationImage: {
      objectFit: "contain",
      height: 165,
      maxWidth: "100%",
      marginTop: 10,
    },
    VerificationBlockText: {
      maxWidth: 260,
      fontSize: 19,
      fontWeight: 600,
      color: "#323A46",
      [theme.breakpoints.down("md")]: {
        fontSize: 12,
      },
    },
    VerificationFormControl: {
      backgroundColor: "#fff",
      "& .MuiSelect-root": {
        border: "none!important",
      },
      "& .MuiInputLabel-formControl": {
        left: 14,
      },

      "& .MuiInputLabel-shrink": {
        left: 0,
      },
      [theme.breakpoints.down("md")]: {
        marginBottom: 10,
      },
    },

    VerificationFormControlWihtoutBg: {
      backgroundColor: "#F7F8F9",
      "& .MuiSelect-root": {
        border: "none!important",
      },
      "& .MuiInputLabel-formControl": {
        left: 14,
      },

      "& .MuiInputLabel-shrink": {
        left: 0,
      },
      [theme.breakpoints.down("md")]: {
        marginBottom: 10,
      },
    },
    wrapper: {
      backgroundColor: "#fff",
      maxWidth: "100%",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      flexDirection: "column",
      borderRadius: 8,
      width: "95vw",
      padding: 16,
    },
    stateImg: {
      width: 166,
      height: 166,
      marginBottom: 16,
      display: "block",
    },
    title: {
      fontSize: 33,
      fontWeight: 500,
      textAlign: "center",
    },
  }));
  const classes = useStyles();

  const [loading, setLoading] = useState(false);
  const firstUpdate = React.useRef(true);
  React.useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (verificationAnswer.severity) {
      setLoading(false);
    }
  }, [verificationAnswer.severity]);

  useEffect(() => {
    if (verificationAnswer.severity === 200) {
      dispatch(getProfileBalance());
      dispatch(getProfileData());
    }
    // eslint-disable-next-line
  }, [verificationAnswer.severity]);

  //console.log("Profile data: ", profileData);

  return (
    <div style={{ color: "#000" }}>
      <Snackbar
        open={verificationAnswer.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() =>
          dispatch(
            setVerification({
              ...alert,
              open: false,
              message: verificationAnswer.message,
            })
          )
        }
      >
        <Alert
          onClose={() => dispatch(setVerification({ ...alert, open: false }))}
          severity={verificationAnswer.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {verificationAnswer.severity === 200
            ? `${t("successfullySent")}`
            : verificationAnswer.message && verificationAnswer.message.messages}
        </Alert>
      </Snackbar>
      {loading && <Reload />}
      {profileData.data?.profile?.verification?.value === "1" ? (
        <>
          <Box display="flex" alignItems="start">
            <img className="icon-md" src={warning} alt="warning" />
            <Typography className={classes.warningText} variant="h5">
              {t("VerificationWarningTextp1")}
            </Typography>
          </Box>
          <Typography className={classes.warningParagraph} variant="h5">
            {t("VerificationWarningTextp4")}
          </Typography>
          <iframe
            src="https://api.netex.kg/sumsub/widget.php"
            width="100%"
            height="1000"
            frameborder="0"
            allow="camera; microphone; geolocation"
          />
        </>
      ) : (
        <Box className={classes.wrapper}>
          <img
            className={classes.stateImg}
            src={
              profileData.data?.profile?.verification?.value === "3"
                ? time
                : profileData.data?.profile?.verification?.value === "2"
                ? Saccess
                : profileData.data?.profile?.verification?.value == "4" && none
            }
            alt="state"
          />
          <Typography className={classes.title}>
            {profileData.data?.profile?.verification?.name}
          </Typography>
          {profileData.data?.profile?.verification?.value == 4 && (
            <>
              <Box display="flex" alignItems="start" sx={{ mt: 2 }}>
                <img className="icon-md" src={warning} alt="warning" />
                <Typography className={classes.warningText} variant="h5">
                  {t("VerificationWarningTextp1")}
                </Typography>
              </Box>
              <Typography className={classes.warningParagraph} variant="h5">
                {t("VerificationWarningTextp4")}
              </Typography>

              <iframe
                src="https://api.netex.kg/sumsub/widget.php"
                width="100%"
                height="1000"
                frameborder="0"
                allow="camera; microphone; geolocation"
              />
            </>
          )}
        </Box>
      )}
    </div>
  );
};

export default Verification;
